


















































































import Vue from 'vue'
import Component from 'vue-class-component'

import {http} from '../../http-requests'
import signUpSuccess from './SignUpSuccess.vue'

import LoggedOutHeader from '@/components/patterns/logged-out-header'


@Component({
  components: {
    signUpSuccess,
    LoggedOutHeader
  }
})
export default class SignUp extends Vue {
  firstName: string = ''
  surname: string = ''
  email: string = ''
  adi: string = ''
  password: string = ''
  confirmPassword: string = ''
  gender: string= ''
  registered: boolean = false
  errorMessage: string = ''

  beforeMount() {
    document.body.className = 'grey-background'    
  }


  /**
   * submit sign up details to the api to create new user model 
   */
  submitSignUp() { 
    if (!this.validate()) { return }

    http.post('instructors', {
      firstName: this.firstName,
      surname: this.surname,
      email: this.email,
      adiLicenseNo: this.adi,
      gender: this.gender,
      password: this.password,
    })
      .then(res => {
        this.registered = true
      })
      .catch(err => { 
        if (err.response.status === 403) {
          this.errorMessage = err.response.data
        }

        throw new Error(err) 
      })
  }

  /**
   * validate inputs
   */
  validate() {
    this.errorMessage = ''

    if (!this.firstName) {
      this.errorMessage = 'First name is required'
      return false
    }

    if (!this.surname) {
      this.errorMessage = 'Surname is required'
      return false
    }

    if (!this.email) {
      this.errorMessage = 'Email is required'
      return false
    }

    if (!this.adi) {
      this.errorMessage = 'ADI license no is required'
      return false
    }

    if (!this.password) {
      this.errorMessage = 'Password is required'
      return false
    }

    if (this.password !== this.confirmPassword) {
      this.errorMessage = 'Confirm password does not match'
      return false
    }

    if (!this.gender) {
      this.errorMessage = 'Gender is required'
      return false
    }

    return true
  }
}



