





























import Vue from 'vue'
import Component from 'vue-class-component'
import {Prop} from 'vue-property-decorator'
import {httpAuth} from '@/http-requests'
import StatusSymbol from '@/components/patterns/status-symbol'
import EditProfileDetails from './children/EditProfileDetails.vue'


@Component({
  components: {
    StatusSymbol,
    EditProfileDetails
  }
})
export default class Profile extends Vue {
  editMode: boolean = false
  updateContactNumber: number = null
  updateHourlyRate: number = 0
  updateOffer: string = ''

  updateResponse = {
    valid: false,
    message: ''
  }

  @Prop() 
    userProfile: {
      adi_license_no: '',
      first_name: '',
      surname: '',
      email: '',
      contact_number: null,
      gender: '',
      verified: false,
      hourly_rate: 0,
      offer: ''
    }


  get fullName() {
    return `${this.userProfile.first_name} ${this.userProfile.surname}`
  }

  /** 
   * when profile updated by child, toggle edit mode off and emit event to parent
   * to get user 
   */
  profileUpdated() {
    this.toggleEditMode()
    this.$emit('profileUpdated')
  }

  /***
   * switch edit mode on/off
   */
  toggleEditMode() {
    this.editMode = !this.editMode
  }
}



