









































import Vue from 'vue'
import Component from 'vue-class-component'
import {http} from '@/http-requests'
import {httpAuth, updateHttpHeader} from '@/http-requests'

import router from '@/router'
import LoggedOutHeader from '@/components/patterns/logged-out-header'


@Component({
  components: {
    LoggedOutHeader
  }
})
export default class Login extends Vue {
  email: string = ''
  password: string = ''
  errorMessage: string = ''
  userType: number = 1

  beforeMount() {
    document.body.className = 'grey-background'
  }
  
  /**
   * submit email and password to api for credential check
   * if successfully store access token recieved in Cookie and Store
   */
  submitLogin() {
    if (!this.validation()) { return }

    http.post('login', {
      email: this.email,
      password: this.password 
    })
    .then(res => {
      localStorage.setItem('token', res.data.access_token)
      updateHttpHeader()

      router.push({name: 'Profile'})
    })
    .catch((err) => {
      this.errorMessage = err.response.data
    })
  }

  /**
   * validate user inputs
   */
  validation() {
    this.errorMessage = ''

    if (!this.email) {
      this.errorMessage = 'Must enter email'
      return false
    }

    if (!this.password) {
      this.errorMessage = 'Must enter password'
      return false
    }

    return true
  }
}



